@import url('https://fonts.googleapis.com/css?family=EB+Garamond');

#page-container {
  position: fixed;
  top: 64px;
  left: 0;
  right: 0;
  bottom: 0;

  overflow-x: hidden;
  overflow-y: auto;
}

#content-container {
  overflow: hidden;

  padding: 40px;
  width: calc(100% - 80px);
}

.text-container {
  left: 0;
  right: 0;

  width: 600px;
  margin: 0 calc(50% - 300px);
}

.text-container > p {
  text-align: justify;

  margin-top: 0;
  font-family: 'EB Garamond', serif;
  font-size: 22px;
}


/* Media queries */

@media (max-width: 1000px) {
  #content-container {
    padding: 30px;
    width: calc(100% - 60px);
  }
}

@media (max-width: 700px) {
  .text-container {
    width: calc(100% - 40px);
    margin: 0 20px;
  }
}

@media (max-width: 599px) {
  #page-container {
    top: 48px;
  }
}

@media (max-width: 480px) {
  #page-container {
    top: 104px;
  }

  #content-container {
    padding: 15px;
    width: calc(100% - 30px);
  }

  .text-container {
    margin-top: 15px;
  }

  .text-container > p {
    text-align: left;
  }
}

@media (max-width: 480px) and (orientation: landscape) {
  #page-container {
    top: 96px;
  }
}



