.banner-container {
  left: 0;
  right: 0;

  /* Fallback Color */
  background-color: #555;

  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;

  position: relative;
  height: 300px;
  overflow: hidden;
}

.banner-background {
  display: block;
  position: absolute;

  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  padding: 0 60px;

  background: rgba(0, 0, 0, 0.4);
}

.banner-text {
  display: block;
  position: relative;

  color: white;
  text-align: center;

  top: 5%;
}

.banner-text > h1 {
  font-family: 'Fjalla One', sans-serif;
  font-size: 64px;
}

.banner-text > p {
  font-size: 24px;
}

.down-arrow {
  width: 50px;

  position: absolute;
  display: none;

  left: 0;
  right: 0;
  margin: auto;
  bottom: 20px;
}


/* Media Queries */

@media (max-width: 1000px), (max-height: 400px) {
  .banner-container {
    height: 200px;
  }

  .banner-text > h1 {
    font-size: 48px;
  }

  .banner-text > p {
    font-size: 20px;
  }
}

@media (max-height: 600px) and (min-width: 481px) {
  .banner-container {
    height: 100%;
  }
  
  .banner-text {
    top: calc(30% - 75px);
  }

  .down-arrow {
    display: block;
  }
}

@media (max-width: 480px) {
  .banner-container {
    height: 96px;
  }

  .banner-text {
    top: 0;
  }

  .banner-text > h1 {
    font-size: 36px;
    margin-bottom: 0px;
  }

  .banner-text > p {
    display: none;
  }
}



