.project-card {
  width: calc(33.3% - 20px);

  margin: 10px;

  overflow: hidden;
  display: block;
  float: left;
}

.project-image {
  height: 200px;
}

.project-header {
  display: block;
  overflow: hidden;
}

.project-title {
  float: left;
}

.project-date {
  float: right;
}

.project-content {
  clear: both;
}

.project-tag {
  margin-left: 6px;
  margin-top: 6px;
}


/* Media Queries */

@media (min-width: 2200px) {
  .project-card {
    width: calc(25% - 20px);
  }
}

@media (max-width: 1300px) {
  .project-card {
    width: calc(50% - 20px);
  }
}

@media (max-width: 850px) {
  .project-card {
    margin: 5px;
    width: calc(100% - 10px);
  }
}



